import frontmatter from 'remark-frontmatter';
import remarkGfm from 'remark-gfm';
import remarkDirective from 'remark-directive';
import remarkMath from 'remark-math';
import { remark } from 'remark';
import { gfmAutolinkLiteralFromMarkdown } from 'mdast-util-gfm-autolink-literal';
import type { MarkdownNode } from './types';

// https://github.com/remarkjs/remark-gfm/issues/16 text  position 
gfmAutolinkLiteralFromMarkdown.transforms = [];

const depsLink = remark()
  .use(frontmatter)
  .use(remarkGfm)
  .use(remarkDirective)
  .use(remarkMath);

/**
 *  Markdown  ast
 *
 * @param {string} md Markdown 
 * @returns {MarkdownNode} md ast 
 * @author YuZhanglong <loveyzl1123@gmail.com>
 */
export const parseMd = (md: string): MarkdownNode => {
  return depsLink.parse(md);
};

/**
 *  ast  markdown
 *
 * @param {MarkdownNode} node ast 
 * @returns {string} md Markdown 
 * @author YuZhanglong <loveyzl1123@gmail.com>
 */
export const revertMdAstNode = (node: MarkdownNode): string => {
  return depsLink.stringify(node);
};
